import React, { Component } from 'react'
import './style.scss'

export default class notFind extends Component {

  goBack(){
    this.props.history.push("/home");
  }

  render() {
    return (
      <div className="page" id="not-find">
        <img src="/img/404.jpg" alt="404"/>
        <p onClick={this.goBack.bind(this)}>
          返回首页
        </p>
      </div>
    )
  }
}
